<template>
  <el-dialog
    v-model="initData.visible"
    :title="initData.title"
    width="700px"
    class="custom-dialog"
    @closed="handleClose"
  >
    <div class="preview-img">
      <span
        v-show="isShowArrow"
        @click="handleArrowLeft"
        class="arrow arrow-left"
      >
        <el-icon><ArrowLeft /></el-icon>
      </span>
      <img :src="initData.curUrl" alt="Preview Image" />
      <span
        v-show="isShowArrow"
        @click="handleArrowRight"
        class="arrow arrow-right"
      >
        <el-icon><ArrowRight /></el-icon>
      </span>
    </div>
  </el-dialog>
</template>

<script setup lang="ts">
import { reactive, computed } from "vue";
import { ArrowRight, ArrowLeft } from "@element-plus/icons-vue";
const initData = reactive({
  title: "",
  visible: false,
  curUrl: "",
  curIndex: -1,
  imgList: [],
});
const isShowArrow = computed(() => {
  return initData.imgList.length > 1;
});
function handleArrowLeft() {
  if (initData.curIndex === 0) {
    initData.curIndex = initData.imgList.length - 1;
  } else {
    initData.curIndex--;
  }
  initData.curUrl = initData.imgList[initData.curIndex];
}
function handleArrowRight() {
  if (initData.curIndex === initData.imgList.length - 1) {
    initData.curIndex = 0;
  } else {
    initData.curIndex++;
  }
  initData.curUrl = initData.imgList[initData.curIndex];
}

function handleClose() {
  initData.visible = false;
  initData.title = "";
  initData.curUrl = "";
  initData.curIndex = -1;
  initData.imgList = [];
}
function trigger(
  visible: boolean,
  title: string,
  imgList: string[],
  index: number
) {
  initData.visible = visible;
  initData.title = title;
  if (index < 0 || index > imgList.length - 1) {
    index = 0;
  }
  initData.curUrl = imgList[index];
  initData.curIndex = index;
  initData.imgList = imgList;
}
defineExpose({
  trigger,
});
</script>

<style scoped lang="scss">
.preview-img {
  position: relative;
  width: 100%;
  padding: 10px;
  user-select: none;
  img {
    display: block;
    width: 100%;
  }
  &:hover {
    .arrow {
      display: inline-flex;
    }
  }
}
.arrow {
  position: absolute;
  top: 50%;
  width: 42px;
  height: 42px;
  display: none;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #ffffff;
  cursor: pointer;
  transform: translateY(-50%);
  border-radius: 100%;
  background-color: rgba(31, 45, 61, 0.2);
  &.arrow-left {
    left: 20px;
  }
  &.arrow-right {
    right: 20px;
  }
}
</style>
